import { useEffect, useState } from 'react';
import { ReactTyped } from 'react-typed';
import avatarImg from '../assets/images/me-avatar.png';
import './Landing.css';

function Landing() {
  const [glitchActive, setGlitchActive] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => setGlitchActive(false), 1100);
    return () => clearTimeout(timer);
  }, []);

  return (
    <header className="landing">
      <div className="hero-layout">
        <img src={avatarImg} alt="Engel Gatus avatar" className="avatar" />

        <div className="intro-block">
          <h1 className="typed-heading">
            <span className={glitchActive ? 'glitch-text' : ''}>
              <ReactTyped
                strings={['HELLO! I AM ENGEL.']}
                typeSpeed={40}
                showCursor={false}
              />
            </span>
          </h1>

          <p className="tagline">
            <span className="tagline-prefix">I'm </span>
            <span className="typed-wrapper">
              <ReactTyped
                strings={[
                  'bridging the gap between what is and what could be.',
                  'creating systems that work for people, not the other way around.',
                  'automating the mundane to unlock the meaningful.',
                  'quietly solving the problems, keep businesses running.',
                  'spotting inefficiencies before they become problems.',
                ]}
                typeSpeed={40}
                startDelay={1500}
                backSpeed={20}
                backDelay={2000}
                loop
              />
            </span>
          </p>

        </div>
      </div>
    </header>
  );
}

export default Landing;
